@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
.App {
  width: 100%;
}

.Editing{max-width:700px;width:100%;margin:0 auto;margin-top:20px}.Editing-form-row{display:flex;justify-content:center;flex-wrap:wrap}.form-item{border:1px solid black;padding:7px}.Item-row{margin-top:30px}.control{background-color:#e9e9e9;cursor:pointer}.control:hover{background-color:darkgrey;color:white}.Filter{margin-top:20px;margin-bottom:20px;display:flex;justify-content:center;width:100%;align-items:center}.lable-filter{margin-right:20px}

.Item{display:flex;justify-content:space-between}.Item-item{margin-right:10px;display:flex;align-items:center;font-size:24px;margin-bottom:10px}.pointer::before{content:"";width:6px;height:6px;border-radius:100%;background-color:black}.operation{margin-left:10px;flex:1 1 auto}.price{margin-right:20px}.material-icons{font-size:24px;cursor:pointer}.edit:hover{color:#5959df}.del{font-size:28px}.del:hover{color:red}

